:root {
  --text: #94a3b8;
  --background: white;
  --accent_0: rgba(255, 102, 94, 30%);
  --accent_1: rgba(255, 72, 176, 45%);
  --accent_2: rgba(255, 72, 176, 05%);
  --accent_3: rgba(0, 0, 0, 45%);
  --accent_4: rgba(0, 0, 0, 10%);
  --accent_5: rgba(61, 85, 136, 100%);
  --accent_6: rgba(61, 85, 136, 0.45);
  --accent_7: rgba(255, 232, 0, 0.5);
  --accent_8: rgba(61, 85, 136, 0.3);
}
.mainContainer {
  display: grid;
  grid-template-columns: clamp(416px, 35%, 40%) 1fr;
  height: 100vh;
  width: 100vw;
}
body {
  font-size: 80%;
}
/* EDITOR (Left Side) */
.parent {
  display: grid;
}
.left {
  display: flex;
  margin: auto;
}

.formContainer {
  font-family: Karla;
  background-color: var(--accent_6);
  padding: 3.5em 2.5em;
  border-radius: 2%;
  display: flex;
  flex-direction: column;
  gap: 2em;
  width: 352px;
  box-shadow: -5px -5px var(--accent_1), 5px 5px var(--accent_7);
}

input,
.description {
  height: 2em;
  width: 100%;
  border: none;
  font-size: 1.3em;
  border-radius: 5px;
  text-indent: 0.5em;
  font-family: Karla;
  color: var(--accent_5);
}
input:focus,
.description:focus {
  outline: none;
}
.description {
  height: 6em;
  padding-top: 0.4em;
}

input.date {
  width: 40%;
}
form ::placeholder {
  color: var(--accent_6);
  font-family: Karla;
}
.dates {
  width: 320px;
  display: flex;
  flex-direction: row;
  gap: 2em;
  justify-content: space-between;
  align-items: center;
}
form,
.formInput,
.job,
.degree,
.skillsDiv,
.honorsDiv {
  display: flex;
  flex-direction: column;
  gap: 1.5em;
}
.job,
.degree {
  margin-bottom: 2em;
}
.jobsDiv,
.degreesDiv {
  display: flex;
  flex-direction: column;
  gap: 2em;
  margin-top: 1em;
}

button {
  font-family: Karla;
  border: none;
  width: max-content;
  height: 2em;
  background-color: #fff;
  font-size: 1.1em;
  color: var(--accent_6);
  border-radius: 10px;
  font-weight: bold;
  display: flex;
  align-items: center;
  gap: 0.5em;
  padding: 1em;
  box-shadow: 4px 4px var(--accent_8);
}
.bottom {
  margin-top: 2em;
  display: flex;
  justify-content: space-between;
  gap: 1em;
}
.trash {
  color: white;
  font-size: 1.7em;
  text-align: center;
}

.flipped {
  transform: scale(-1);
}

/* PREVIEW (Right-side) */
.right {
  background-color: var(--accent_8);
  display: flex;
  justify-content: center;
  flex-direction: row;
}
.paper {
  width: 800px;
  height: 1036px;
  background-color: white;
  position: relative;
}
.previewContainer {
  margin: 2em;
}

.fullSize {
  zoom: 145%;
  position: absolute;
  right: 0;
}
.icons {
  display: flex;
  flex-direction: column;
  margin-top: 4em;
  gap: 2em;
}

.zoomIn,
.zoomOut,
.download {
  color: white;
  font-size: 2em;
}
.cvText {
  padding: 3em 7em 3em 6em;
  display: flex;
  flex-direction: row;
  gap: 5em;
}

.name {
  font-size: 3em;
  font-weight: bold;
}
.about {
  margin-top: 0.5em;
  font-size: 1.5em;
  font-style: italic;
}
.title {
  font-size: 1.5em;
}
.sectionTitle {
  font-weight: bold;
  text-transform: uppercase;
}
.cvLeft {
  display: flex;
  flex-direction: column;
  gap: 15em;
}
.cvBottomLeft {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 8em;
}

.cvTopRight {
  margin-top: 3em;
}
.cvBottomRight {
  margin-top: clamp(5em, 8em, 10em);
  display: flex;
  flex-direction: column;
  gap: 2em;
}

.jobsTop,
.degreeTop {
  display: flex;
  justify-content: space-between;
}
.degreeType {
  font-style: italic;
}
.displayDegreesDiv {
  display: flex;
  flex-direction: column;
  gap: 1em;
  margin-top: 1em;
}
.jobDescription {
  text-align: justify;
  margin-top: 0.5em;
}
.jobDates,
.degreeDates {
  display: flex;
}
.organizationTitle {
  font-style: italic;
}

.awardsDiv {
  margin-top: 1em;
  display: flex;
  flex-direction: column;
  gap: 0.25em;
}
